class CartManager {
  setCircularConfig(config) {
    localStorage.setItem("circularMapConfig", JSON.stringify(config));
  }
  getCircularConfig() {
    return JSON.parse(localStorage.getItem("circularMapConfig"));
  }
  getCircularOrder() {
    const config = this.getCircularConfig();
    return {
      type: "circular",
      bounds: config.bounds,
      theme: config.theme.value,
      density: config.density.value,
      cordColor: config.cordColor.value,
      frameFinish: config.frameFinish.value
    };
  }
}

const instance = new CartManager();
Object.freeze(CartManager);

export default instance;
