import * as Sentry from "@sentry/browser";

const LAMBDA_URL =
  "https://xdc9dmik2f.execute-api.us-west-2.amazonaws.com/Prod/submit_order/";

// yeah, not the best thing to do here
// https://medium.com/este-js-framework/its-ok-to-use-javascript-generated-guid-in-a-browser-373ca6431cf7
export const generateOrderId = () => {
  var x = 2147483648;
  return (
    Math.floor(Math.random() * x).toString(36) +
    Math.abs(Math.floor(Math.random() * x) ^ Date.now()).toString(36)
  );
};

export const submitOrderToBackend = chargeParams => {
  return new Promise((resolve, reject) => {
    try {
      const xhr = new XMLHttpRequest();
      xhr.open("POST", LAMBDA_URL, true);
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.responseType = "json";
      xhr.onload = () => {
        const jsonResponse = xhr.response;
        const e = jsonResponse.error;
        if (e) {
          Sentry.captureException(e);
          reject(e);
          return;
        }
        resolve(jsonResponse);
      };
      xhr.send(JSON.stringify({ chargeParams }));
    } catch (e) {
      Sentry.captureException(e);
      reject("Unknown error. Please email support@maplamps.com.");
    }
  });
};
